import React, { useState } from "react";
import momentJalaali from "moment-jalaali";
import DatePicker from "react-datepicker2";
import notification from "helpers/notification";
// reactstrap components
import {
  Card,
  CardHeader,
  CardBody,
  FormGroup,
  Button,
  Label,
  Input,
  CardTitle,
  CardFooter,
} from "reactstrap";

// api
import ApiPost from "API/post";

export default (props) => {
  const [date, setDate] = useState(momentJalaali());
  const [milkWeight, setMilkWeight] = useState("");
  const [milkWeightError, setMilkWeightError] = useState("");

  const handleSubmit = () => {
    console.log("clicked");
    if (!milkWeight) {
      setMilkWeightError("       ");
    } else {
      const data = {
        entry: {
          value: milkWeight,
          date: date,
          key: props.animalKey,
        },
        token: localStorage.artimal,
      };

      ApiPost("api/v0/milk", data)
        .then((res) => {
          if (milkWeightError) setMilkWeightError("");
          console.log(res);
          notification(res.result, "success");
          props.forceUpdate();
        })
        .catch((err) => {
          console.log("errrrrrrrrrrr", err);
          if (err.error) {
            notification(err.error, "danger");
          } else {
            notification("   ", "danger");
          }
        });

      console.log(data);
    }
  };

  return (
    <Card className="demo-icons">
      <CardHeader>
        <CardTitle tag="h6"></CardTitle>
      </CardHeader>
      <CardBody>
        <FormGroup>
          <DatePicker
            isGregorian={false}
            timePicker={false}
            value={date}
            onChange={(value) => setDate(value)}
          />
        </FormGroup>
        <FormGroup>
          <Label></Label>
          <FormGroup>
            <Input
              placeholder=""
              value={milkWeight}
              onChange={(e) => {
                setMilkWeight(e.target.value);
              }}
              type="number"
            />
          </FormGroup>
          {milkWeightError ? (
            <p className="error-text-form">{milkWeightError}</p>
          ) : null}
        </FormGroup>
        <FormGroup>
          <Button type="button" color="primary" onClick={handleSubmit}>
            
          </Button>
        </FormGroup>
      </CardBody>

      <CardFooter className="button-group"></CardFooter>
    </Card>
  );
};
