import React, { useEffect, useState } from "react";
import momentJalaali from "moment-jalaali";

// reactstrap components
import { Table, Row, Col, Button, FormGroup, Label, Input } from "reactstrap";

// API
import ApiGet from "API/get";

export default () => {
  const [logs, setLogs] = useState([]);
  const [page, setpage] = useState();
  const [uri, setUri] = useState("");
  const [animalKey, setAnimalKey] = useState("");

  // const [key, setKey] = useState("");
  const [value, setValue] = useState("");
  const [collection, setCollection] = useState("");

  if (typeof page !== "number") setpage(0);

  useEffect(() => {
    console.log("send request");
    ApiGet(uri)
      .then((res) => {
        console.log("..........", res);
        setLogs(res.result);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [uri]);

  useEffect(() => {
    const _collection = () => {
      switch (collection) {
        case "":
          return "";
        case "":
          return "weight";
        case "":
          return "milk";
        case "":
          return "disease";
        case "":
          return "income";
        case "":
          return "vaccine";
        case "":
          return "pregnancy";
        case "":
          return "expense";
        default:
          return "";
      }
    };
    // <option></option>
    // <option></option>
    // <option> </option>

    const _value = () => {
      switch (value) {
        case "":
          return "";
        case "":
          return "create";
        case "":
          return "update";
        case " ":
          return "delete";
        default:
          return "";
      }
    };

    console.log(
      "000000000000000000000000",
      typeof _collection(),
      _collection()
    );

    let query = `${_collection() ? `collection:${_collection()}` : ""}${
      _value() ? `&&value:${_value()}` : ""
    }${animalKey ? `&&animalKey:${animalKey}` : ""}`;

    console.log("=======================", query, typeof query);

    setUri(`api/v0/logs/pagination/20/${page}/${query}`);
  }, [page, collection, value, animalKey]);

  /* -------------------------------------------------------------------------- */
  /*                                  handlers                                  */
  /* -------------------------------------------------------------------------- */
  const handleBody = () => {
    return logs.map((log) => {
      const createdAtJdate = momentJalaali(log.createdAt).format("jYYYY-jM-jD");
      return (
        <tr key={log._key}>
          <td>{createdAtJdate}</td>
          <td>{log.value}</td>
          <td>{log.type}</td>
          <td>{log.entryId}</td>
          <td>{log.userId}</td>
          <td>{log.animalId}</td>
        </tr>
      );
    });
  };

  return (
    <div className="content">
      <Row>
        <Col md="4">
          <FormGroup>
            <Label></Label>
            <FormGroup>
              <Input
                className="select-input"
                type="select"
                value={collection}
                onChange={(e) => setCollection(e.target.value)}
              >
                <option></option>
                <option></option>
                <option></option>
                <option></option>
                <option></option>
                <option></option>
                <option></option>
                <option></option>
              </Input>
            </FormGroup>
          </FormGroup>
        </Col>
        <Col md="4">
          <FormGroup>
            <Label></Label>
            <FormGroup>
              <Input
                className="select-input"
                type="select"
                value={value}
                onChange={(e) => setValue(e.target.value)}
              >
                <option></option>
                <option></option>
                <option></option>
                <option> </option>
              </Input>
            </FormGroup>
          </FormGroup>
        </Col>
        <Col md="4">
          <FormGroup>
            <Label for="exampleText"> </Label>
            <Input
              type="text"
              value={animalKey}
              onChange={(e) => setAnimalKey(e.target.value)}
            />
          </FormGroup>
        </Col>
      </Row>
      <Row>
        <Col md="12">
          {logs.length ? (
            <Table>
              <thead className="text-primary">
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th>   </th>
                  <th></th>
                  <th> </th>
                </tr>
              </thead>
              <tbody>{handleBody()}</tbody>
            </Table>
          ) : null}
          <p>{page}</p>
        </Col>
        <Col md="12">
          <Button
            type="button"
            onClick={() => setpage(page + 1)}
            color="primary"
          >
            
          </Button>
          {page >= 1 ? (
            <Button
              type="button"
              onClick={() => setpage(page - 1)}
              color="secondary"
            >
              
            </Button>
          ) : null}
        </Col>
      </Row>
    </div>
  );
};
